/* color vars
 * may not be totally x-browser safe but yolo
 */
html {
  --darkBlue: #5f727f;
  --red:      #E74C3C;
  --white:    #ECF0F1;
  --grey:     #DDDDDD;
  --skyBlue:  #6689a0;
  --blue:     #2980B9;
  --green:    #18b518;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

button {
  -webkit-appearance: none;
  color: #b33;
  background: none;
  border: none;
  outline: none;
}

html, body {
  background: white;
  color: var(--skyBlue);
  font-size: 16px;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.view--title {
  font-size: 1.4rem;
  margin: 1em 0 1em;
}

.view--title h4 {
  margin: 0;
}

.view .btn-change-mode {
  color: var(--skyBlue);
}

#root {
  text-align: center;
  width: 100%;
}

.input-box {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 0 2rem;
}

.input-box .label {
  font-size: 1.1rem;
  margin: 0 0 0.5rem;
}

.input-box .input {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  width: 90%;
  padding: 6px;
  font-size: 26px;
  height: 42px;
  border: 1px solid #ccc;
  background: #fafafa;
  border-radius: 2px;
  color: #333;
  font-weight: 300;
}

.input-box.locked .input {
  background: #e3e3e3;
}

.input-box.focus {
  position: absolute;
  top: 0;
  height: 100%;
  z-index: 1;
  margin: 0;
  background: white;
}

.input-box.focus .input {
  /*background: #ddd;*/
  border: 1px solid #557;
  outline: none;
}


.keypad {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-left: 1px solid var(--white);
  border-bottom: 1px solid var(--white);
  z-index: 1;
  height: 60%;
}

.keypad-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 0 33%;
      flex: 1 0 33%;
  background: var(--white);
  border: 1px solid var(--grey);
  color: var(--skyBlue);
  border-bottom: none;
  border-left: none;
  font-size: 32px;
  font-weight: 300;
}

.keypad-button.check {
  background: #efe;
}


/**
 * inputbox pre-populated values.
 *
 */

.input-box-pre-values .btn-choose {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1.25rem;
  color: var(--skyBlue);
}


.input-box-pre-values.focus {
  z-index: 2; /* above keypad */
  background: white;
}

.input-box-pre-values .list .list-item {
  display: block;
  width: 100%;
  padding: 1em;
  font-size: 1rem;
  background: #eee;
  color: var(--blue);
}

.input-box-pre-values .list .list-item:nth-child(even) {
  background: white
}

/** drinks view */
.drinks {
  padding: 12px;
}

.drinks .sentence {
  font-size: 1.25rem;
  line-height: 1.1em;
  margin: 1 0 1em;
}

.drinks .sentence + .sentence {
  margin: 0 0 1.5em;
}

.drinks .sentence span {
  white-space: nowrap;
}

/** drinks summary header */
.drinks-summary p {
  font-weight: 600;
  font-size: 1rem;
  margin: 0;
}

/** individual drink */
.drink {
  background: var(--skyBlue);
  padding: 8px;
  border-top: 1px solid var(--darkBlue);
  color: var(--darkBlue);
}

.drink:nth-child(even) {
  background: var(--blue);
}

.drink .meta, .drink .actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.drink .meta .timestamp, .drink .meta .vol-abv {
  -ms-flex: 2 1;
      flex: 2 1;
  text-align: left;
}

.drink .meta p {
  margin: 0;
}

.drink .meta .sd {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
}


.drink .actions {
  margin: 16px 0 0;
}

.readmore .references {
  text-align: left;
  word-break: break-word;
}

.readmore .references li {
  margin: 0 0 0.5em;
}

.readmore .references li a {
  color: inherit;
}

/* buttons  */

.btn {
  background: none;
  border-radius: 2px;
  border: 1px solid var(--skyBlue);
  padding: 12px;
  color: var(--darkBlue);
  font-size: 1rem;
  margin: 0 auto;
  width: 90%;
}

.btn:disabled {
  color: #aaa;
}

.btn.secondary {
  border: 1px solid #b33;
  color: #b33;
  background: #fee;
  font-size: 0.8rem;
}

.btn.tertiary {
  border: none;
  color: #b96b14;
  font-size: 0.8rem;
  text-decoration: underline;
}



#root, #router, .App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.App {
  -ms-flex-direction: column;
      flex-direction: column;
}

.App > header {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  display: -ms-flexbox;
  display: flex;
  background: var(--blue);
  border-bottom: 2px solid white;
  padding: 8px;
}

.App > header .drinks-summary {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App .sidebar-wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
}


.view {
  -ms-flex: 1 1;
      flex: 1 1; /* height */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0;
}

.keypad {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.calc-form {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
}

.calc-instructions {
  padding: 0 5%;
  font-size: 0.9rem;
  margin: 0 0 2em;
}


.settings .success-notice {
  color: var(--green);
  line-height: 34px;
}

.calculator--btn-switch {
  margin: 0.5em 0;
  color: var(--skyBlue);
  font-size: 1rem;
}


/* splash */
.splash {
  text-align: center;
  padding: 32px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-animation: fadein 0.5s ease-in-out;
          animation: fadein 0.5s ease-in-out;
}

h1 {
  color: var(--darkBlue);
  font-weight: 200;
  font-size: 2rem;
}

header > h1 {
  background: #fafafa;
  border-bottom: 1px solid #eee;
  padding: 12px;
  margin: 0 0 24px;
  font-size: 1.2rem;
}

.splash h1 {
  margin-top: 25%;
}


/* spinner */
.spinner svg {
  -webkit-animation: rotate 2s cubic-bezier(0.5, 0.35, 0.5, 0.65) infinite;
          animation: rotate 2s cubic-bezier(0.5, 0.35, 0.5, 0.65) infinite;
}


/* ButtonLoader */
.button-loader .wrapper {
  position: relative;
}

.button-loader .spinner, .button-loader .button-loader-children {
  -webkit-transition: opacity 0.1s ease-in-out;
  -o-transition: opacity 0.1s ease-in-out;
  transition: opacity 0.1s ease-in-out;
}

.button-loader .spinner {
  position: absolute;
  width: 100%;
  text-align: center;
}

.button-loader .spinner {
  opacity: 0;
}

.button-loader.loading .button-loader-children {
  opacity: 0;
}

.button-loader.loading .spinner {
  opacity: 1;
}


/* animations */
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}


