.drinks-sentence, .drinks-sentence--safe {
  margin: 0;
  font-size: 1rem;
}

.drinks-sentence--highlight {
  margin: 6px 0;
  color: #bfa22b;
  font-size: 1.1rem;
}

.drinks-sentence--safe {
  color: var(--green);
  margin: 6px 0;
}

.feed-at-summary {
  border-bottom: 1px solid #eee;
  padding-bottom: 24px;
  margin: 0 auto 24px;
  width: 80%;
}


.drink-list {
  margin-top: 24px;
  padding: 5%;
}

.drink-list-item {
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #eee;
  border-radius: 2px;
  -ms-flex-align: center;
      align-items: center;
  padding: 12px 0;
}

.drink-list-item .icon {
  font-size: 30px;
  -ms-flex: none;
      flex: none;
  margin: 0 12px;
  color: var(--blue);
}

.drink-list-item .icon + .icon {
  margin-left: 0;
}

.drink-list-item span {
  font-size: 1.2rem;
  font-weight: 200;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: left;
}

.drink-list-item .progress-bar {

}



